* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  text-align: center;
  box-sizing: border-box;
  height: 100%;
  background-color: #0a0909;
  margin-top: 100px;
  font-family: prompt;
  font-size: 35px;
  line-height: 70px;
}

header {
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}

h1 {
  color: #f07178;
}

.price-tracker {
  background: linear-gradient(to left, #30cfd0 0%, #613a91 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.price-tracker:hover {
  opacity: .8;
  transition: all .3s;
}

.tracker {
  width: 100%;
  height: 65px;
  background-color: #1e2222;
  margin-top: 100px;
  font-family: Courier New, Courier, monospace;
  display: flex;
}

.coins {
  gap: 20px;
  list-style: none;
  display: flex;
  position: relative;
}

p {
  color: gray;
  margin: 0;
  font-size: 15px;
}

.price {
  color: #cdc7c7;
  font-weight: 800;
}

img {
  width: 1rem;
  height: 1rem;
}

.tracker li {
  opacity: 1;
  justify-content: space-between;
  animation: 15s linear infinite coin-slide;
  transform: translateX(100%);
}

@keyframes coin-slide {
  0% {
    transform: translateX(0);
  }

  10% {
    transform: translateX(-100px);
  }

  25% {
    transform: translateX(-200px);
  }

  50% {
    transform: translateX(-400px);
  }

  75% {
    transform: translateX(-600px);
  }

  100% {
    transform: translateX(-800px);
  }
}

